import { FC } from "react";
import { motion } from "motion/react"
import Logo from "./Logo";
import { X } from "lucide-react";
import { Button } from "./ui/button";

interface sideBarProps {
    isOpen: boolean;
    onClose: () => void;
}

const SideBar: FC<sideBarProps> = ({ isOpen, onClose }) => {
    return (
        <div className="fixed inset-y-0 left-0 z-50 bg-darkColor/50 shadow-xl hoverEffect w-full">
            <motion.div className="min-w-72 max-w-96 bg-darkColor text-white/90 h-full p-10 border-r border-r-white flex flex-col gap-6">
                <Button className="hover:text-red-500 hoverEffect "><X /></Button>
                <div className="flex items-center justify-between">
                    <Logo className="text-white">Comfort</Logo>
                </div>
            </motion.div>
        </div>
    )
}
export default SideBar